// 6.axios的拦截器
// fn1: 请求发送 成功 时执行的函数
// fn2: 请求发送 失败 时执行的函数
// axios请求拦截
// axios.interceptors.request.use(fn1,fn2)
axios.interceptors.request.use(config => {
    // 想做的一些操作
    // 1.给请求添加token
    // isLoading动画
    return config;
}, err => {
    console.log('请求发生错误');
    return err;
});
// axios响应拦截
// axios.interceptors.response.use(fn1,fn2)
axios.interceptors.response.use(res => {
    console.log('响应成功');
    return res;
}, err => {
    console.log('服务器响应失败!');
    return err;
});
// 5.axios.all -> 多个请求，一起返回
axios.all([
    axios.get('/get', { params: { name: 'dxt', age: 18 } }),
    axios.post('/post', { data: { name: 'dxt', age: 18 } }),
]).then(res => {
    console.log(res);
});
//  Promise本身是可以有类型
